import React from 'react';
import {Box, Grid, IconButton, Stack, styled,useMediaQuery,useTheme} from "@mui/material";
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';
import MenuIcon from '@mui/icons-material/Menu';
import Logo from '../assets/logo.png'

const JinIconButton = styled(IconButton)(({theme})=>({
    color:'rgb(119,119,119)',
    height:'30px',
    width:'30px',
    component:'a',
    '&:hover':{
        color:'white',
        transform:'scale(1.3)'
    }
}))

function Navbar({setState}) {
    const theme = useTheme()
    const isMatch = useMediaQuery(theme.breakpoints.down('md'))

    return (
        <Grid container spacing={2} padding={1}>
            <Grid item xs={8}>
                <Box sx={{height:'60px',":hover":{
                        height:'65px'
                }
                }}>
                    <img src={Logo} height={'100%'}/>
                </Box>

            </Grid>
            <Grid item xs={4} display={'flex'} flexDirection={'row-reverse'}>
                {
                    isMatch?(
                        <Box onClick={()=>setState(true)}>
                            <JinIconButton >
                                <MenuIcon />
                            </JinIconButton>
                        </Box>
                    ):(
                        <>
                            <JinIconButton  href="mailto:jinpeng8111765@gmail.com" >
                                <EmailIcon />
                            </JinIconButton>

                            <JinIconButton href="https://www.linkedin.com/in/peng-jin-37b832218/" target="_blank">
                                <LinkedInIcon />
                            </JinIconButton>

                            <JinIconButton  href="https://github.com/pejoyjp" target="_blank">
                                <GitHubIcon  />
                            </JinIconButton>
                        </>

                    )
                }

            </Grid>
        </Grid>
    );
}

export default Navbar;