import React from 'react';
import {Box, Typography, Rating, Grid} from "@mui/material";
import {data} from "./consts/data.js";
function LeftSide({value}) {
    return (
        <Box flex={3}>
            <Box
                 sx={{
                     marginTop:{
                         xs:'10px',
                         lg:'100px'
                     },
                     marginLeft:{
                         xs:'20px',
                         lg:'110px'
                     },
                     marginRight:{
                         xs:'20px',
                         lg:'110px'
                     },
                     maxWidth:'1300px',
                     height:'200px'
                 }}>
                {
                    value===0?(
                        <Box>
                        <Typography variant={'h5'} color={'#ccc'} >
                            Jin
                        </Typography>

                        <Typography variant={'body1'} color={'#ccc'} mt={'20px'}>
                            My name is JIN, a noob full-stack developer. Born in 1998, currently live in Ireland. I have been working as a web developer for the past one years. In that time, I have gained experience in both back-end and front-end development. I have also worked on a wide range of projects, from small, simple websites to large, complex ones. My skills include HTML, CSS, JavaScript, React, and Node.js.

                        </Typography>

                            <Typography variant={'body1'} color={'#ccc'} mt={'10px'}>
                                Being a developer is a cool thing, I love what I do and want to be in it for the rest of my life.
                            </Typography>

                        </Box>
                    ):<></>

                }

                {
                    value===1?(
                        <Box>
                            <Typography variant={'h5'} color={'#ccc'} >
                                Skill
                            </Typography>
                            <Typography variant={'body1'} color={'#ccc'} mt={'20px'}>
                                I have a strong interest in many new technologies. This may be the reason why I can't specialize in a language xd. My skills include Js, Ts, React, C and some engineering knowledge. My bachelor is in electrical engineering. So I am quite good at mathematics and algorithms.
                            </Typography>
                            <Typography variant={'body1'} color={'#ccc'} mt={'10px'}>
                                Here are my main skills and how I rate them.
                            </Typography>
                            <Box sx={{display:'flex',flexDirection:'column',gap:'15px',marginTop:'20px'}}>

                                <Grid container spacing={2}>
                                    {
                                        data.map((item)=>(
                                            <Grid item xs={12} lg={6} key={item.id} sx={{display:'flex',alignItems:'center',gap:'15px'}}>
                                                <img src={item.img} width={50}/>
                                                <Typography variant={'h5'} color={'#ccc'}>
                                                    {item.name}
                                                </Typography>
                                                <Rating name="half-rating-read" defaultValue={item.rate} precision={0.5} readOnly />
                                            </Grid>
                                        ))
                                    }
                                </Grid>


                            </Box>
                        </Box>
                    ):<></>
                }
                {
                    value===2?(
                        <Box>
                            <Typography variant={'h5'} color={'#ccc'} >
                                Cranky
                            </Typography>
                            <Typography variant={'body2'} color={'#ccc'} mt={'20px'}>
                                君子曰：學不可以已。
                                青，取之於藍，而青於藍；冰，水為之，而寒於水。木直中繩，輮以為輪，其麯中規。雖有槁暴，不複挺者，輮使之然也。故木受繩則直，金就礪則利，君子博學而日參省乎己，則知明而行無過矣。
                                故不登高山，不知天之高也；不臨深溪，不知地之厚也；不聞先王之遺言，不知學問之大也。幹、越、夷、貉之子，生而同聲，長而異俗，教使之然也。詩曰：“嗟爾君子，無恆安息。靖共爾位，好是正直。神之聽之，介爾景福。”神莫大於化道，福莫長於無禍。
                                吾嘗終日而思矣，不如須臾之所學也；吾嘗跂而望矣，不如登高之博見也。登高而招，臂非加長也，而見者遠；順風而呼，聲非加疾也，而聞者彰。假輿馬者，非利足也，而緻仟裏；假舟楫者，非能水也，而絕江河。君子生非異也，善假於物也。　　南方有鳥焉，名曰蒙鳩，以羽為巢，而編之以發，係之葦苕，風至苕摺，卵破子死。巢非不完也，所係者然也。西方有木焉，名曰射幹，莖長四寸，生於高山之上，而臨百仞之淵，木莖非能長也，所立者然也。蓬生麻中，不扶而直；白沙在涅，與之俱黑。蘭槐之根是為芷，其漸之滫，君子不近，庶人不服。其質非不美也，所漸者然也。故君子居必擇鄉，遊必就士，所以防邪辟而近中正也。
                            </Typography>
                        </Box>
                    ):(
                        <Box>

                        </Box>
                    )
                }


            </Box>
        </Box>
    );
}

export default LeftSide;