import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';

import List from '@mui/material/List';

import ListItem from '@mui/material/ListItem';

import ListItemText from '@mui/material/ListItemText';
import {Tab, Tabs} from "@mui/material";
import {useState} from "react";



function DrawerCom({state,setState,value,setValue}) {

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    return (
        <Box onClick={()=>setState(!state)}>
            <Drawer
                open={state}
            >
                <Tabs value={value}
                      onChange={handleChange}
                      orientation="vertical"
                      textColor='primary'
                      sx={{backgroundColor:'rgba(0,0,0,.8)',height:'100%'}}
                >
                    <Tab label="introduction" sx={{color:'#ccc'}} />
                    <Tab label="Skill" sx={{color:'#ccc'}}/>
                    <Tab label="cranky" sx={{color:'#ccc'}}/>
                </Tabs>

            </Drawer>
        </Box>
    );
}

export default DrawerCom;