import React, {useEffect, useState} from 'react';
import {Box, Tab, Tabs, Typography} from "@mui/material";
import Navbar from "./Navbar.jsx";
import LeftSide from "./LeftSide.jsx";
import DrawerCom from "./DrawerCom.jsx";


function Feed({setState,state,value,setValue}) {

    const [text,setText] = useState('jin')
    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
            <Box sx={{
                minHeight:{
                    xs:'800px',
                    md:'730px'
                },
                backgroundColor:'rgb(29,29,32)',
                borderRadius:'10px',
                border:'3px solid #ccc',
                margin:'10px'
            }}>
                <Navbar setState={setState}/>
                <Box sx={{display:'flex'}}>
                    <LeftSide  value={value} />
                    <Box flex={2}
                         sx={{display:{xs:'none',md:'flex'},justifyContent:'center',alignItems:'center',flexDirection:'column',gap:'20px'}}
                    >
                        <Typography variant={'h5'} color={'#ccc'} >
                            About Me
                        </Typography>
                        <Tabs value={value}
                              onChange={handleChange}
                              orientation="vertical"
                              textColor='primary'
                        >
                            <Tab label="introduction" sx={{color:'#ccc'}} />
                            <Tab label="Skill" sx={{color:'#ccc'}}/>
                            <Tab label="cranky" sx={{color:'#ccc'}}/>
                        </Tabs>
                    </Box>
                </Box>
                <DrawerCom state={state} setState={setState} value={value} setValue={setValue}/>

            </Box>


    );
}

export default Feed;